// src/utils/validation.ts - Input validation and XSS protection
import { z } from 'zod';
import DOMPurify from 'dompurify';

// Zod schemas for validation
export const ComponentSchema = z.object({
  name: z.string().min(1, 'Component name is required').max(100, 'Component name too long'),
  category: z.string().max(50, 'Category name too long').default('Other'),
  html: z.string().max(50000, 'HTML content too large').default(''),
  css: z.string().max(50000, 'CSS content too large').default(''),
  js: z.string().max(50000, 'JavaScript content too large').default(''),
  tags: z.union([
    z.string(),
    z.array(z.string())
  ]).transform((val) => {
    if (typeof val === 'string') {
      return val.split(',').map(tag => tag.trim()).filter(Boolean);
    }
    return val;
  }).default([])
});

export const UpdateComponentSchema = ComponentSchema.partial().refine(
  (data) => Object.keys(data).length > 0,
  'At least one field must be provided for update'
);

// XSS protection configuration
const purifyConfig = {
  // Allow common HTML tags and attributes
  ALLOWED_TAGS: [
    'div', 'span', 'p', 'h1', 'h2', 'h3', 'h4', 'h5', 'h6',
    'strong', 'b', 'em', 'i', 'u', 'ul', 'ol', 'li',
    'a', 'img', 'br', 'hr', 'table', 'tr', 'td', 'th',
    'button', 'input', 'form', 'label', 'select', 'option',
    'textarea', 'section', 'article', 'header', 'footer',
    'nav', 'main', 'aside'
  ],
  ALLOWED_ATTR: [
    'class', 'id', 'style', 'src', 'alt', 'href', 'target',
    'type', 'value', 'placeholder', 'name', 'for', 'role',
    'aria-*', 'data-*', 'title'
  ],
  // Forbid script tags and event handlers
  FORBID_TAGS: ['script', 'object', 'embed', 'link', 'meta', 'base'],
  FORBID_ATTR: ['onload', 'onerror', 'onclick', 'onmouseover', 'onfocus', 'onblur'],
  // Remove scripts and style URLs
  FORBID_CONTENTS: ['script'],
  // Clean up URLs
  SANITIZE_DOM: true,
  RETURN_DOM: false,
  RETURN_DOM_FRAGMENT: false,
  RETURN_DOM_IMPORT: false
};

/**
 * Sanitize HTML content to prevent XSS attacks
 */
export function sanitizeHTML(html: string): string {
  if (!html || typeof html !== 'string') return '';
  
  return DOMPurify.sanitize(html, {
    ...purifyConfig,
    // More strict for HTML content
    FORBID_ATTR: [...purifyConfig.FORBID_ATTR, 'javascript:']
  });
}

/**
 * Sanitize CSS content to prevent CSS injection attacks
 */
export function sanitizeCSS(css: string): string {
  if (!css || typeof css !== 'string') return '';
  
  // Remove dangerous CSS patterns
  const dangerousPatterns = [
    /javascript:/gi,
    /expression\s*\(/gi,
    /behavior\s*:/gi,
    /binding\s*:/gi,
    /@import/gi,
    /url\s*\(\s*["']?\s*javascript:/gi,
    /url\s*\(\s*["']?\s*data:/gi,
    /vbscript:/gi
  ];
  
  let sanitized = css;
  dangerousPatterns.forEach(pattern => {
    sanitized = sanitized.replace(pattern, '');
  });
  
  // Remove potential XSS in CSS comments
  sanitized = sanitized.replace(/\/\*[\s\S]*?\*\//g, '');
  
  return sanitized;
}

/**
 * Sanitize JavaScript content (very strict)
 */
export function sanitizeJS(js: string): string {
  if (!js || typeof js !== 'string') return '';
  
  // For now, we'll be very conservative and only allow basic operations
  // In a real-world scenario, you might want to use a JS parser/sanitizer
  const dangerousPatterns = [
    /eval\s*\(/gi,
    /Function\s*\(/gi,
    /setTimeout\s*\(/gi,
    /setInterval\s*\(/gi,
    /document\.write/gi,
    /document\.cookie/gi,
    /localStorage/gi,
    /sessionStorage/gi,
    /XMLHttpRequest/gi,
    /fetch\s*\(/gi,
    /window\./gi,
    /location\./gi,
    /navigator\./gi,
    /history\./gi,
    /import\s*\(/gi,
    /require\s*\(/gi
  ];
  
  let sanitized = js;
  dangerousPatterns.forEach(pattern => {
    sanitized = sanitized.replace(pattern, '');
  });
  
  // Remove potentially dangerous characters and sequences
  sanitized = sanitized.replace(/<script/gi, '');
  sanitized = sanitized.replace(/<\/script>/gi, '');
  
  return sanitized;
}

/**
 * Validate and sanitize component data
 */
export function validateAndSanitizeComponent(data: any) {
  // First validate the structure
  const validationResult = ComponentSchema.safeParse(data);
  
  if (!validationResult.success) {
    return {
      success: false,
      error: 'Validation failed',
      details: validationResult.error.errors.map(err => ({
        field: err.path.join('.'),
        message: err.message
      }))
    };
  }
  
  const validated = validationResult.data;
  
  // Then sanitize the content
  const sanitized = {
    ...validated,
    name: validated.name.trim(),
    category: validated.category.trim(),
    html: sanitizeHTML(validated.html),
    css: sanitizeCSS(validated.css),
    js: sanitizeJS(validated.js),
    tags: validated.tags.map((tag: string) => tag.trim()).filter(Boolean)
  };
  
  return {
    success: true,
    data: sanitized
  };
}

/**
 * Validate and sanitize component update data
 */
export function validateAndSanitizeUpdate(data: any) {
  // First validate the structure
  const validationResult = UpdateComponentSchema.safeParse(data);
  
  if (!validationResult.success) {
    return {
      success: false,
      error: 'Validation failed',
      details: validationResult.error.errors.map(err => ({
        field: err.path.join('.'),
        message: err.message
      }))
    };
  }
  
  const validated = validationResult.data;
  
  // Then sanitize the content
  const sanitized: any = {};
  
  if (validated.name !== undefined) {
    sanitized.name = validated.name.trim();
  }
  if (validated.category !== undefined) {
    sanitized.category = validated.category.trim();
  }
  if (validated.html !== undefined) {
    sanitized.html = sanitizeHTML(validated.html);
  }
  if (validated.css !== undefined) {
    sanitized.css = sanitizeCSS(validated.css);
  }
  if (validated.js !== undefined) {
    sanitized.js = sanitizeJS(validated.js);
  }
  if (validated.tags !== undefined) {
    sanitized.tags = validated.tags.map((tag: string) => tag.trim()).filter(Boolean);
  }
  
  return {
    success: true,
    data: sanitized
  };
}

// Export types for use in other files
export type ComponentData = z.infer<typeof ComponentSchema>;
export type UpdateComponentData = z.infer<typeof UpdateComponentSchema>;